header{
    position: sticky;
    top: 0;
    height: 60px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
    background-color: #FFFFD0;
    color: black;
    z-index: 100;
}

.header1{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    
}
.none1{
    text-decoration: none;
    color: black;
}
.none{
    text-decoration: none;
    color: black;
}
.icons{
    display: flex;
    gap: 20px;
}

.icons span{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    position: relative;

}
.icon{
    font-size: 30px;
    color: black;
}

.cart_count{
    display: flex;
    justify-content: center;
    align-items: center;
}
.count{
    position: absolute;
    right: 10px;
    top: 6px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: red;
    color: #ffff;
}

.btn123,
.btn123::after {
 padding: 16px 20px;
 font-size: 8px;
 background: linear-gradient(45deg, transparent 5%, #ff013c 5%);
 border: 0;
 color: #fff;
 letter-spacing: 3px;
 line-height: 1;
 box-shadow: 6px 0px 0px #00e6f6;
 outline: transparent;
 position: relative;
}

.btn123::after {
 --slice-0: inset(50% 50% 50% 50%);
 --slice-1: inset(80% -6px 0 0);
 --slice-2: inset(50% -6px 30% 0);
 --slice-3: inset(10% -6px 85% 0);
 --slice-4: inset(40% -6px 43% 0);
 --slice-5: inset(80% -6px 5% 0);
 content: "HOVER ME";
 display: block;
 position: absolute;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;
 background: linear-gradient(45deg, transparent 3%, #00e6f6 3%, #00e6f6 5%, #ff013c 5%);
 text-shadow: -3px -3px 0px #f8f005, 3px 3px 0px #00e6f6;
 clip-path: var(--slice-0);
}

.btn123:hover::after {
 animation: 1s glitch;
 animation-timing-function: steps(2, end);
}

@keyframes glitch {
 0% {
  clip-path: var(--slice-1);
  transform: translate(-20px, -10px);
 }

 10% {
  clip-path: var(--slice-3);
  transform: translate(10px, 10px);
 }

 20% {
  clip-path: var(--slice-1);
  transform: translate(-10px, 10px);
 }

 30% {
  clip-path: var(--slice-3);
  transform: translate(0px, 5px);
 }

 40% {
  clip-path: var(--slice-2);
  transform: translate(-5px, 0px);
 }

 50% {
  clip-path: var(--slice-3);
  transform: translate(5px, 0px);
 }

 60% {
  clip-path: var(--slice-4);
  transform: translate(5px, 10px);
 }

 70% {
  clip-path: var(--slice-2);
  transform: translate(-10px, 10px);
 }

 80% {
  clip-path: var(--slice-5);
  transform: translate(20px, -10px);
 }

 90% {
  clip-path: var(--slice-1);
  transform: translate(-10px, 0px);
 }

 100% {
  clip-path: var(--slice-1);
  transform: translate(0);
 }
}


.nav_btn{
    padding: 5px;
    cursor: pointer;
    background-color: transparent;
    border: none;
    outline: none;
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
}

@media  screen and (max-width: 1024px){
    .nav_btn{
        visibility: visible;
        opacity: 1;
    }

    .header1{
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-color: #FFFFD0;
        gap: 1.5rem;
        transition: 1s;
        transform: translateY(100vh);
        font-size: 1.5rem;
        z-index: 10;
    }
    
    .header .responsive_nav{
        transform: none;
    }


}
@media  screen and (max-width: 600px){
    .nav_heading h1{
        font-size: 20px;
    }
    .btn123{
        display: none;
    }

}
